<template>
  <form @submit.prevent="handleSubmit"
        ref="form"
        id="data-form-container"
        class="data-form-container">
    <div class="form-item">
      <div class="input-area">
        <input maxlength="10" type="text"
               v-model="formData.nickname"
               placeholder="用户昵称">
        <span class="tip">{{ formData.nickname.length }}/10</span>
      </div>
      <div class="error">{{ error.nickname }}</div>
    </div>
    <div class="form-item">
      <div class="text-area">
        <textarea maxlength="300" placeholder="输入内容"
                  v-model="formData.content"
        ></textarea>
        <span class="tip">{{ formData.content.length }}/300</span>
      </div>
      <div class="error">{{ error.content }}</div>
    </div>
    <div class="form-item">
        <LightButton
            :button-content="isSubmitting?'提交中':'提交'"
            :class="{disabled:isSubmitting}"/>
    </div>
  </form>
</template>

<script>
import LightButton from "@/components/LightButton"
export default {
  name: "DataForm",
  components: {
    LightButton
  },
  data(){
    return {
      formData:{
        nickname: "",
        content: ""
      },
      error: {
        nickname: "",
        content: ""
      },
      isSubmitting: false
    }
  },
  methods: {
    handleSubmit(){
      this.error.nickname = this.formData.nickname ? "" : "请填写昵称";
      this.error.content = this.formData.content ? "" : "请输入内容";
      if(this.error.nickname || this.error.content){
        //有错误
        return;
      }
      this.isSubmitting = true;//正在提交，防止重复点击
      this.$emit("submit", this.formData, (successMsg) => {
        this.$showMessage({
          content: successMsg,
          type: "success",
          duration: "1000",
          container: this.$refs.form,
          callback: () => {
            this.isSubmitting = false;
            this.formData.nickname = "";
            this.formData.content = "";
          }
        })
      });//让父组件来处理事件
    }
  }
}
</script>

<style scoped lang="less">
@import "~@/styles/var.less";
.data-form-container {
  margin-bottom: 20px;
  overflow: hidden;
  padding: 10px;
}
.form-item {
  margin-bottom: 8px;
}
.input-area {
  width: 50%;
  position: relative;
}
.text-area {
  position: relative;
}
.tip {
  position: absolute;
  right: 5px;
  bottom: 5px;
  color: #b4b8bc;
  font-size: 12px;
}
input,
textarea {
  display: block;
  width: 100%;
  box-sizing: border-box;
  border: 1px dashed @gray;
  outline: none;
  color: @words;
  font-size: 14px;
  border-radius: 4px;
  &:focus {
    border-color: @primary;
  }
}
input {
  padding: 0 15px;
  height: 40px;
}
textarea {
  resize: none;
  padding: 8px 15px;
  height: 120px;
}
.error {
  margin-top: 6px;
  color: @danger;
  font-size: 14px;
  height: 20px;
  line-height: 20px;
}
.button-container{
  position: relative;
  cursor: pointer;
  border: none;
  outline: none;
  border-radius: 4px;
}
</style>